import React from 'react';
import { useParams, useHistory } from 'react-router-dom';
import moment from 'moment';
import { Button, Box, InputAdornment, FormControlLabel, Checkbox, FormGroup, s, Avatar } from '@material-ui/core';
import {
  MailOutline as IconMailOutline,
  AccountCircle as IconAccountCircle,
  Lock as IconLock,
  Visibility as IconVisiblility,
  VisibilityOff as IconVisibilityOff,
  Alarm as IconAlarm,
  LocationOn as IconLocationOn,
} from '@material-ui/icons';
import { withStyles } from '@material-ui/core/styles';
import { TextValidator, ValidatorForm } from 'react-material-ui-form-validator';
import { Autocomplete } from '@material-ui/lab';

import { getUserByIdFunc, updateUserProfile } from '../../actions/user';

import Navbar from '../../Components/Navbar/Navbar';
import useStyleProfile from './styleProfile';
import ConfirmDialog from '../../Components/Confirm/ConfirmDialog';

const Profile = () => {
  const classes = useStyleProfile();
  const history = useHistory();
  const params = useParams();
  const initialProfile = {
    email: '',
    fullName: '',
    timezone: '',
    url: '',
    password: '',

    availability: { days: {}, hours: { start: null, end: null } },
  };
  const [profile, setProfile] = React.useState({});
  const [hours, setHours] = React.useState({});
  const [workDays, setWorkDays] = React.useState({});
  const [confirm, setConfirm] = React.useState('');
  const [openDialog, setOpenDialog] = React.useState(false);
  const handleChange = (e) => {
    setProfile({ ...profile, [e.target.name]: e.target.value });
  };

  const handleDays = (e) => {
    setWorkDays({ ...workDays, [e.target.name]: e.target.checked });
  };

  const handleUpdateProfile = () => {
    setProfile({ ...profile, availability: { hours: hours, days: workDays } });
    updateUserProfile(profile)
      .then((res) => {
        console.log(res.data);
      })
      .catch((err) => console.log(err));
  };
  React.useEffect(() => {
    getUserByIdFunc(params.id)
      .then((res) => {
        res.data && setProfile({ ...profile, email: res.data.email });
        setProfile({ ...profile, _id: res.data._id });
        setProfile({ ...profile, fullName: res.data.fullName });
        setProfile({ ...profile, url: res.data.url });
        setWorkDays(res.data.availability.days);
        setHours(res.data.availability.hours);
        setProfile({ ...profile, timezone: res.data.timezone });
        setProfile({ ...profile, password: '' });
        // setProfile({ ...profile, confirm: '' });
      })
      .catch((err) => {
        setProfile(initialProfile);
      });
    ValidatorForm.removeValidationRule('isPasswordMatch');
  }, []);

  React.useEffect(() => {
    ValidatorForm.addValidationRule('isPasswordMatch', (value) => {
      if (value !== profile.password) {
        console.log(profile.password, value);
        return false;
      }
      return true;
    });
  }, [confirm]);

  const OrangeCheckbox = withStyles((theme) => ({
    root: {
      color: theme.palette.primary.light,
      '&$checked': {
        color: theme.palette.primary.main,
      },
    },
    checked: {},
  }))((props) => <Checkbox color="default" {...props} />);

  const renderCheckBoxes = (days) => {
    console.log(days, profile);
    return Object.keys(days).map((day) => {
      return (
        <FormControlLabel
          id={day + '-checkbox-field'}
          classes={{ root: classes.checkBoxLabel, label: classes.checkBoxLabelLabel }}
          key={day}
          control={<OrangeCheckbox checked={days[day]} name={day} onChange={handleDays} />}
          label={day + 's'}
          labelPlacement="bottom"
        />
      );
    });
  };
  return (
    <Box className={classes.container}>
      <Navbar picture={profile && profile.picture} name={profile && profile.fullName} />
      <ValidatorForm onSubmit={handleUpdateProfile}>
        <Box className={classes.subHeader}>
          <Box>
            <Box mb={4} display="flex" justifyContent={'center'}>
              <Avatar style={{ width: '100px', height: '100px' }} />
            </Box>
            <Box mb={4}>
              <TextValidator
                fullWidth
                label="Email"
                name="email"
                variant={'outlined'}
                value={profile.email}
                type="email"
                validators={['required', 'isEmail']}
                errorMessages={['Email field is required', 'email is not valid']}
                onChange={handleChange}
                InputProps={{
                  startAdornment: (
                    <InputAdornment className={classes.adornment} position="start" component="div">
                      <IconMailOutline />
                    </InputAdornment>
                  ),
                }}
              />
            </Box>
            <Box mb={4}>
              <TextValidator
                fullWidth
                label={'FullName'}
                name="fullName"
                variant={'outlined'}
                value={profile.fullName}
                type="text"
                validators={['required']}
                errorMessages={['FullName field is required']}
                onChange={handleChange}
                InputProps={{
                  startAdornment: (
                    <InputAdornment className={classes.adornment} position="start" component="div">
                      <IconAccountCircle />
                    </InputAdornment>
                  ),
                }}
              />
            </Box>
          </Box>
          <Box mb={4}>
            <TextValidator
              // className={classes.urlField}
              fullWidth
              id="url-field"
              label="URL"
              variant="outlined"
              value={profile && profile.url}
              validators={['required']}
              errorMessages={['URL field is required']}
              onChange={handleChange}
              // onChange={(e) => setUrl(e.target.value)}
              InputProps={{
                startAdornment: (
                  <InputAdornment className={classes.adornment} position="start" component="div">
                    http://dentoconnect.herokuapp.com/
                  </InputAdornment>
                ),
              }}
            />
          </Box>
          <Box mb={4}>
            <Autocomplete
              id="timezone-field"
              options={moment.tz.names()}
              getOptionLabel={(option) => option + ' (GMT' + moment.tz(option).format('Z') + ')'}
              // onChange={(e, v) => (v)}
              onChange={(e, v) => setProfile({ ...profile, timezone: v })}
              value={profile.timezone}
              renderInput={(params) => (
                <TextValidator
                  {...params}
                  label="Type a city or zone"
                  variant="outlined"
                  validators={['required']}
                  errorMessages={['TImeZone field is required']}
                />
              )}
            />
          </Box>
          <Box mb={4}>
            <Box className={classes.hoursRow}>
              <TextValidator
                fullWidth
                id="start-hours-field"
                value={hours.start}
                variant="outlined"
                type="time"
                label="From"
                name="start"
                validators={['required']}
                errorMessages={['StartTIme field is required']}
                onChange={(e) => {
                  setHours({ ...hours, start: e.target.value });
                }}
                InputProps={{
                  startAdornment: (
                    <InputAdornment className={classes.adornment} position="start" component="div">
                      <IconAlarm />
                    </InputAdornment>
                  ),
                }}
                // onChange={handleHours}
              />
              <span>-</span>
              <TextValidator
                id="end-hours-field"
                fullWidth
                value={hours.end}
                variant="outlined"
                type="time"
                label="To"
                name="end"
                onChange={(e) => {
                  setHours({ ...hours, end: e.target.value });
                  console.log(hours);
                }}
                validators={['required']}
                errorMessages={['EndTime field is required']}
                InputProps={{
                  startAdornment: (
                    <InputAdornment className={classes.adornment} position="start" component="div">
                      <IconAlarm />
                    </InputAdornment>
                  ),
                }}
                // onChange={handleHours}
              />
            </Box>
          </Box>
          <Box mb={4}>
            <div>Available Days: </div>
            <FormGroup row className={classes.checkBoxGroup}>
              {renderCheckBoxes(workDays && workDays)}
            </FormGroup>
          </Box>
          <Box mb={4}>
            <TextValidator
              fullWidth
              name="password"
              label="New Password"
              value={profile.password}
              type="password"
              variant="outlined"
              onChange={handleChange}
              InputProps={{
                startAdornment: (
                  <InputAdornment className={classes.adornment} position="start" component="div">
                    <IconLock />
                  </InputAdornment>
                ),
              }}
            />
          </Box>
          <Box mb={4}>
            <TextValidator
              fullWidth
              name="confirm"
              value={confirm}
              onChange={(e) => {
                setConfirm(e.target.value);
              }}
              type="password"
              label="Confirm New Passwords"
              variant="outlined"
              validators={['isPasswordMatch']}
              errorMessages={['password mismatch']}
              InputProps={{
                startAdornment: (
                  <InputAdornment className={classes.adornment} position="start" component="div">
                    <IconLock />
                  </InputAdornment>
                ),
              }}
            />
          </Box>
          <Box display={'flex'} justifyContent="flex-end">
            <Box mr={4}>
              <Button color="primary" variant="outlined" type="submit">
                Save
              </Button>
            </Box>
            <Box>
              <Button
                color="primary"
                variant="outlined"
                onClick={(e) => {
                  history.push('/dashboard');
                }}
              >
                Calcel
              </Button>
            </Box>
          </Box>
        </Box>
      </ValidatorForm>
      <ConfirmDialog
        onOK={handleUpdateProfile}
        onCancel={(e) => setOpenDialog(false)}
        title={'Confirm Dialog'}
        content={`Would you sure delete this EventSchedule?`}
        openDialog={openDialog}
      />
    </Box>
  );
};

export default Profile;
